"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link" // <-- PERBAIKAN: Import Link sudah ditambahkan
import { ShieldAlert, Lock, User, ArrowRight } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"

export default function AdminLoginPage() {
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const router = useRouter()

  const handleAdminLogin = (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    // SIMULASI LOGIN ADMIN
    // Username & Password Admin sementara di-hardcode untuk testing
    setTimeout(() => {
      if (username === "superadmin" && password === "nusantara2025") {
        localStorage.setItem("user_logged_in", "true")
        localStorage.setItem("user_role", "admin")
        router.push("/admin")
      } else {
        alert("Akses Ditolak: Kredensial Admin Salah!")
        setIsLoading(false)
      }
    }, 1500)
  }

  return (
    <main className="min-h-screen bg-[#1a1a1a] flex items-center justify-center p-6 font-sans">
      <Card className="w-full max-w-md p-8 bg-[#262626] border-none rounded-3xl shadow-[0_20px_50px_rgba(0,0,0,0.5)]">
        <div className="text-center mb-10">
          <div className="bg-red-500/10 w-20 h-20 rounded-3xl flex items-center justify-center mx-auto mb-4 border border-red-500/20">
            <ShieldAlert className="text-red-500 w-10 h-10" />
          </div>
          <h1 className="text-white text-3xl font-bold tracking-tight">Restricted Access</h1>
          <p className="text-gray-500 mt-2 text-sm uppercase tracking-widest font-semibold">Administrator Only</p>
        </div>

        <form onSubmit={handleAdminLogin} className="space-y-6">
          <div className="space-y-2">
            <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">Admin Username</label>
            <div className="relative">
              <User className="absolute left-4 top-3.5 w-5 h-5 text-gray-500" />
              <Input 
                type="text" 
                placeholder="ID Administrator" 
                className="pl-12 py-6 bg-[#1a1a1a] border-none text-white focus:ring-red-500 rounded-2xl"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <label className="text-xs font-bold text-gray-400 uppercase tracking-wider ml-1">Master Password</label>
            <div className="relative">
              <Lock className="absolute left-4 top-3.5 w-5 h-5 text-gray-500" />
              <Input 
                type="password" 
                placeholder="••••••••••••" 
                className="pl-12 py-6 bg-[#1a1a1a] border-none text-white focus:ring-red-500 rounded-2xl"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
          </div>

          <Button 
            type="submit" 
            disabled={isLoading}
            className="w-full py-8 bg-red-600 hover:bg-red-700 text-white font-bold rounded-2xl text-lg transition-all flex gap-2 items-center justify-center"
          >
            {isLoading ? (
              <div className="h-6 w-6 animate-spin rounded-full border-2 border-white border-t-transparent"></div>
            ) : (
              <>Otorisasi Masuk <ArrowRight className="w-5 h-5" /></>
            )}
          </Button>
        </form>

        <div className="mt-10 text-center">
          <Link href="/" className="text-gray-600 text-sm hover:text-gray-400 transition-colors">
            Kembali ke Web Utama
          </Link>
        </div>
      </Card>
    </main>
  )
}